<template>
  <div class="training-container">
    <div class="training-form">
      <h3 class="training-form-title">  .</h3>
      <div class="training-form-radio" v-for="(item, index) in list" :key="index">
        <el-radio v-model="radio" :label="item.value">{{ item.text }}</el-radio>
      </div>
    </div>
    <div class="training-btn" v-if="radio">
      <router-link :to="{ name: 'trainingQuestion', query: { type: radio } }" class="link"> </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TrainingType',
  data () {
    return {
      active: false,
      radio: null,
      list: [
        { value: 'basic', text: '' },
        { value: 'financial', text: '' },
        { value: 'sales', text: '' },
        { value: 'crewmember', text: '' }
      ]
    }
  }
}
</script>



