<template>
  <div class="form">
    <form @submit.prevent="onSubmit">
      <div class="form-group" :class="{ 'status': $v.form.email.$error }">
        <label for="email" class="form-group-title"><span class="text-danger">*</span>  (Man2sfix@man2sfix.com)</label>
        <at-input type="email" v-model.trim="$v.form.email.$model" id="email" :status="$v.form.email.$error ? 'error' : ''" size="large"></at-input>
        <div class="form-group-feedback" v-if="!$v.form.email.required"> .</div>
        <div class="form-group-feedback" v-if="!$v.form.email.email">  .</div>
      </div>
      <div class="form-group" :class="{ 'status': $v.form.password.$error }">
        <label for="password" class="form-group-title"><span class="text-danger">*</span> </label>
        <at-input type="password" v-model.trim="$v.form.password.$model" id="password" :status="$v.form.password.$error ? 'error' : ''" size="large"></at-input>
        <div class="form-group-feedback" v-if="!$v.form.password.required"> .</div>
      </div>
      <at-button hollow class="form-btn" nativeType="submit"></at-button>
      <div class="form-link">
        <router-link :to="'/find-account'" class="btn-link">/ ?</router-link>
        <router-link :to="'/signup'" class="btn-link">  ?</router-link>
      </div>
    </form>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, email } from 'vuelidate/lib/validators'

export default {
  name: 'SignInForm',
  mixins: [
    validationMixin
  ],
  data () {
    return {
      loading: false,
      form: {
        email: '',
        password: ''
      }
    }
  },
  validations: {
    form: {
      email: {
        required,
        email
      },
      password: {
        required
      }
    }
  },
  methods: {
    async onSubmit () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        //  
        this.loading = true
        //   
        const formData = {
          email: this.form.email,
          password: this.form.password
        }
        //  boolean
        const bool = await this.$store.dispatch('signIn', formData)
        //   
        if (bool) {
          this.$message({
            showClose: true,
            message: ' .',
            type: 'success'
          })
          this.$router.replace(this.$route.query.redirect || '/')
        }
        //  
        this.loading = false
      } else {
        return false
      }
    }
  }
}
</script>



