<template>
  <div class="form">
    <form @submit.prevent="onSubmit">
      <div class="form-group" :class="{ 'status': $v.form.name.$error }">
        <label for="name" class="form-group-title"><span class="text-danger">*</span>  ()</label>
        <at-input v-model.trim="$v.form.name.$model" class="form-control" id="name" :status="$v.form.name.$error ? 'error' : ''" size="large"></at-input>
        <div class="form-group-feedback" v-if="!$v.form.name.required"> .</div>
        <div class="form-group-feedback" v-if="!$v.form.name.minLength || !$v.form.name.maxLength"> 2  5  .</div>
      </div>
      <div class="form-group" :class="{ 'status': $v.form.email.$error }">
        <label for="email" class="form-group-title"><span class="text-danger">*</span>  (Man2sfix@man2sfix.com)</label>
        <at-input type="email" v-model.trim="$v.form.email.$model" id="email" :status="$v.form.email.$error ? 'error' : ''" size="large"></at-input>
        <div>
          <at-checkbox v-model="form.emailAgree" label="emailAgree">  </at-checkbox>
        </div>
        <div class="form-group-feedback" v-if="!$v.form.email.required"> .</div>
        <div class="form-group-feedback" v-if="!$v.form.email.email">  .</div>
      </div>
      <div class="form-group" :class="{ 'status': $v.form.password.$error }">
        <label for="password" class="form-group-title"><span class="text-danger">*</span> </label>
        <at-input type="password" v-model.trim="$v.form.password.$model" :status="$v.form.password.$error ? 'error' : ''" size="large" id="password"></at-input>
        <div class="form-group-feedback" v-if="!$v.form.password.required"> .</div>
        <div class="form-group-feedback" v-if="!$v.form.password.minLength">  6  .</div>
      </div>
      <div class="form-group" :class="{ 'status': $v.form.passwordConfirm.$error }">
        <label for="passwordConfirm" class="form-group-title"><span class="text-danger">*</span>  </label>
        <at-input type="password" v-model.trim="$v.form.passwordConfirm.$model" :status="$v.form.passwordConfirm.$error ? 'error' : ''" size="large" id="passwordConfirm"></at-input>
        <div class="form-group-feedback" v-if="!$v.form.passwordConfirm.required || !$v.form.passwordConfirm.sameAsPassword"> .</div>
      </div>
      <div class="form-group" :class="{ 'status': $v.form.phone.$error }">
        <label for="phone" class="form-group-title"><span class="text-danger">*</span>   (010-0000-0000)</label>
        <at-input v-model.trim="$v.form.phone.$model" id="phone" :status="$v.form.phone.$error ? 'error' : ''" size="large"></at-input>
        <div>
          <at-checkbox v-model="form.phoneAgree" label="phoneAgree">SMS  </at-checkbox>
        </div>
        <div class="form-group-feedback" v-if="!$v.form.phone.required">  .</div>
        <div class="form-group-feedback" v-if="!$v.form.phone.helpers">   .</div>
      </div>
      <div class="form-group" :class="{ 'status': $v.form.terms.$error }">
        <label class="form-group-title"><span class="text-danger">*</span> </label>
        <div>
          <at-checkbox v-model="form.terms" label="terms"></at-checkbox>
        </div>
        <at-button type="primary" hollow class="btn-detail" @click="termsModal = true"> </at-button>
        <div class="form-group-feedback" v-if="!$v.form.terms.between"> .</div>
      </div>
      <div class="form-group" :class="{ 'status': $v.form.privacy.$error }">
        <label class="form-group-title"><span class="text-danger">*</span>  </label>
        <div>
          <at-checkbox v-model="form.privacy" label="privacy"></at-checkbox>
        </div>
        <at-button type="primary" class="btn-detail" hollow @click="privacyModal = true"></at-button>
        <div class="form-group-feedback" v-if="!$v.form.privacy.between">  .</div>
      </div>
      <at-button hollow class="form-btn" nativeType="submit"></at-button>
    </form>
    <!-- modal -->
    <at-modal v-model="termsModal">
      <div slot="header">
        <span></span>
      </div>
      <div style="max-height: 300px; overflow-y: scroll;">
        <sign-up-terms></sign-up-terms>
      </div>
      <div slot="footer">
        <at-button style="width:100%;" @click="termsModal = false"></at-button>
      </div>
    </at-modal>
    <!-- modal -->
    <at-modal v-model="privacyModal">
      <div slot="header">
        <span> </span>
      </div>
      <div style="max-height: 300px; overflow-y: scroll;">
        <sign-up-privacy></sign-up-privacy>
      </div>
      <div slot="footer">
        <at-button style="width:100%;" @click="privacyModal = false"></at-button>
      </div>
    </at-modal>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate'
import { required, email, minLength, maxLength, sameAs, helpers, between } from 'vuelidate/lib/validators'

import SignUpTerms from '@/components/auth/SignUpTerms'
import SignUpPrivacy from '@/components/auth/SignUpPrivacy'

export default {
  name: 'SignUpStudent',
  mixins: [
    validationMixin
  ],
  data () {
    return {
      loading: false,
      termsModal: false,
      privacyModal: false,
      form: {
        name: '',
        email: '',
        password: '',
        passwordConfirm: '',
        phone: '',
        profile: '',
        terms: false,
        privacy: false,
        emailAgree: true,
        phoneAgree: true
      }
    }
  },
  validations: {
    form: {
      name: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(5)
      },
      email: {
        required,
        email
      },
      password: {
        required,
        minLength: minLength(6)
      },
      passwordConfirm: {
        required,
        sameAsPassword: sameAs('password')
      },
      phone: {
        required,
        helpers: helpers.regex('phone', /^\d{3}-\d{3,4}-\d{4}$/)
      },
      terms: {
        between: between(1, 1)
      },
      privacy: {
        between: between(1, 1)
      }
    }
  },
  methods: {
    async onSubmit () {
      this.$v.$touch()
      if (!this.$v.$invalid) {
        //  
        this.loading = true
        //   set
        let formData = new FormData()
        formData.append('type', 'student')
        formData.append('name', this.form.name)
        formData.append('email', this.form.email)
        formData.append('password', this.form.password)
        formData.append('phone', this.form.phone)
        formData.append('profile', this.form.profile)
        formData.append('terms', this.form.terms)
        formData.append('privacy', this.form.privacy)
        formData.append('email_agree', this.form.emailAgree)
        formData.append('phone_agree', this.form.phoneAgree)
        //  boolean
        const bool = await this.$store.dispatch('signUp', formData)
        //   
        if (bool) {
          this.$message({
            showClose: true,
            message: ' .',
            type: 'success'
          })
          this.$router.replace('/signin')
        }
        //  
        this.loading = false
      }
    }
  },
  components: {
    SignUpTerms,
    SignUpPrivacy
  }
}
</script>



