<template>
  <div class="training-container">
    <div class="training-form">
      <h3 class="training-form-title">  .</h3>
      <div class="training-form-radio" v-for="(item, index) in list" :key="index">
        <el-radio v-model="radio" :label="item.value">{{ item.text }}</el-radio>
      </div>
    </div>
    <div class="training-btn" v-if="radio">
      <router-link :to="{ name: 'trainingType' }" class="link"></router-link>
      <router-link :to="{ name: 'trainingVideo', query: { question: radio, type: type } }" class="link" v-if="radio"></router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TrainingQuestion',
  data () {
    return {
      type: '',
      radio: null,
      list: []
    }
  },
  methods: {
    init () {
      this.type = this.$route.query.type
    },
    fetchData () {
      this.list = [
        { value: 1, text: '    ?' },
        { value: 2, text: '      ?' },
        { value: 3, text: '      .' },
        { value: 4, text: '    ?' },
        { value: 5, text: '     ?' }
      ]
    }
  },
  watch: {
    '$route': 'init, fetchData'
  },
  created () {
    this.init()
    this.fetchData()
  }
}
</script>

<style lang="scss" scoped>

</style>



