<template>
  <div class="inner-contents">
    <h4 class="contents-title-sub"></h4>
    <div class="form" v-loading="loading">
      <el-form ref="form" :model="form" :rules="rules" label-position="top" size="medium" @submit.prevent.native="onSubmit('form')">
        <el-form-item label=" " prop="type">
          <el-select v-model="form.type" placeholder=" ">
            <el-option label="" value="alliance"></el-option>
            <el-option label="" value="instructor"></el-option>
            <el-option label="" value="problem"></el-option>
            <el-option label="" value="error"></el-option>
            <el-option label="" value="etc"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label=" " prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label=" " prop="description">
          <el-input type="textarea" v-model="form.description" :autosize="{ minRows: 3, maxRows: 10}"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button native-type="submit" plain class="btn-block"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ContactForm',
  data () {
    return {
      loading: false,
      form: {
        type: '',
        email: '',
        title: '',
        description: ''
      },
      rules: {
        type: [
          { required: true, message: '  .', trigger: 'change' }
        ],
        email: [
          { required: true, message: ' .', trigger: 'blur' },
          { type: 'email', message: '  .', trigger: 'blur' }
        ],
        title: [
          { required: true, message: '  .', trigger: 'blur' }
        ],
        description: [
          { required: true, message: '  .', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // todo
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.contents-title-sub {
  max-width: 800px;
  margin: 0 auto;
}

.form {
  max-width: 800px;
  margin: map-get($spacers, 3) auto;

  .el-select {
    width: 100%;
  }
}
</style>



