<template>
  <app-layout>
    <div class="inner-contents" v-loading="pageLoading">
      <h2 class="contents-title"> </h2>
      <div class="form" v-loading="loading">
        <el-form ref="form" :model="form" :rules="rules" label-position="top" size="medium" @submit.prevent.native="onSubmit('form')">
          <el-form-item label="" prop="name">
            <el-input v-model="form.name" placeholder=") "></el-input>
          </el-form-item>
          <el-form-item label=" (  .)" prop="email">
            <el-input v-model="form.email" placeholder=") man2sfix@man2sfix.com" readonly></el-input>
            <el-checkbox v-model="form.emailAgree"> </el-checkbox>
          </el-form-item>
          <el-form-item label=" " prop="phone">
            <div class="item-phone">
              <el-input type="text" v-model="form.phone" placeholder=") 010-0000-0000"></el-input>
            </div>
            <el-checkbox v-model="form.phoneAgree">SMS </el-checkbox>
          </el-form-item>
          <el-form-item label="" class="relative-box">
            <el-upload list-type="picture" action="#" :auto-upload="false" :limit="1" :on-change="uploadProfile" :on-remove="removeUpdateProfile" :on-exceed="exceedProfile" :file-list="fileList">
              <el-button size="small" plain></el-button>
              <div slot="tip" class="el-upload__tip">1MB  gif/jpg/png  .</div>
            </el-upload>
          </el-form-item>
          <el-form-item>
            <el-button native-type="submit" plain class="btn-block"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </app-layout>
</template>

<script>
import Validator from '@/mixins/validator/Validator'
import UploadProfile from '@/mixins/upload/UploadProfile'
import AppLayout from '@/components/layout/AppLayout'

export default {
  name: 'ChangeInfo',
  mixins: [
    Validator,
    UploadProfile
  ],
  data () {
    return {
      loading: false,
      pageLoading: true,
      user: '',
      fileList: [],
      form: {
        id: '',
        name: '',
        email: '',
        phone: '',
        profile: '',
        profileURL: '',
        profileRemove: false,
        emailAgree: true,
        phoneAgree: true
      },
      rules: {
        name: [
          { required: true, message: ' .', trigger: 'blur' },
          { min: 2, max: 5, message: ' 2  5  .', trigger: 'blur' }
        ],
        email: [
          { required: true, message: ' .', trigger: 'blur' },
          { type: 'email', message: '  .', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '  .', trigger: 'blur' },
          { validator: this.validatePhone, message: '   .', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    async init () {
      this.user = await this.$store.dispatch('getUser')
      // set form data
      this.form.id = this.user.mem_id
      this.form.name = this.user.mem_name
      this.form.email = this.user.mem_email
      this.form.phone = this.user.mem_phone
      this.form.emailAgree = this.user.mem_email_agree
      this.form.phoneAgree = this.user.mem_phone_agree
      this.form.profileURL = this.user.mem_profile
      // file list
      if (this.user.mem_profile) {
        const fileArr = this.user.mem_profile.split('/')
        this.fileList.push({
          name: fileArr[fileArr.length - 1],
          url: `${this.$api}/${fileArr.join('/')}`
        })
      }
      //  
      this.pageLoading = false
    },
    onSubmit (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          console.log(this.form)
          //  
          this.loading = true
          //   set
          let formData = new FormData()
          formData.append('id', this.form.id)
          formData.append('name', this.form.name)
          formData.append('email', this.form.email)
          formData.append('phone', this.form.phone)
          formData.append('email_agree', this.form.emailAgree)
          formData.append('phone_agree', this.form.phoneAgree)
          formData.append('profileURL', this.form.profileURL)
          formData.append('profileRemove', this.form.profileRemove)
          if (this.form.profile) {
            formData.append('profile', this.form.profile)
          }
          //  boolean
          const bool = await this.$store.dispatch('chageInfo', formData)
          //   
          if (bool) {
            this.$message({
              showClose: true,
              message: ' .',
              type: 'success'
            })
            this.$router.replace('/mypage')
          }
          //  
          this.loading = false
        } else {
          return false
        }
      })
    }
  },
  components: {
    AppLayout
  },
  watch: {
    '$route': 'init'
  },
  created () {
    this.init()
  }
}
</script>

<style lang="scss" scoped>

</style>



